n.darkmode .upvote {\n  background: #424656;\n  color: #fff;\n}\n\n.darkmode .upvote:hover {\n  background: #f3f3f3;\n  color: #1c1e29;\n}\n\n.svgicon {\n  width: 18px;\n}\n\n.broadcast-component.is-open {\n  visibility: visible;\n  opacity: 1;\n  transition: 200ms;\n  box-shadow: 4px 0px 8px 4px #54565f3b;\n  left: 0;\n}\n\n.broadcast_improvement {\n  color: #0ea2e2;\n}\n\n.broadcast_admin_name, .broadcast_time_ago {\n  color: #676879;\n  font-size: 13px;\n}\n\n.broadcast_general {\n  color: #f6607d;\n}\n\n.broadcast_message {\n  margin-bottom: 15px;\n  font-size: 14px;\n}\n.broadcast-component #broadcast_frame .broadcast_message img {\n  height: initial;\n  max-height: 280px;\n  max-width: 578px;\n}\n.broadcast_message>img {\n  max-width: 245px;\n  margin: 10px 0px;\n  display: block;\n}\n.broadcast-component #broadcast_frame .broadcast_admin_photo {\n  top: 14px;\n  left: 16px;\n  width: 35px;\n  height: 35px;\n}\n.broadcast_admin_photo {\n  position: absolute;\n  top: 8px;\n  left: 8px;\n  width: 34px;\n  height: 34px;\n  border-radius: 50%;\n}\n\n.broadcast-component #broadcast_frame .broadcast_notification {\n  padding: 12px 34px 10px 64px;\n}\n\n#broadcast_list li.broadcast_notification {\n  margin-left: 0px;\n  min-height: 65px;\n  padding: 12px 12px 10px 52px;\n  border-bottom: 1px solid #eee;\n  position: relative;\n}\n\n.broadcast-component {\n  visibility: hidden;\n  left: -700px;\n  opacity: 0;\n  -webkit-animation-timing-function: cubic-bezier(0, 0, 0.35, 1);\n  animation-timing-function: cubic-bezier(0, 0, 0.35, 1);\n  box-shadow: none;\n  height: 100%;\n  width: 700px;\n  overflow-y: scroll;\n  display: flex;\n  flex-direction: column;\n  background-color: white;\n  position: fixed;\n  z-index: 999999;\n}\n\n.broadcast-component .broadcast-title {\n  flex: 0 0 60px;\n  background-color: #fbfbfb;\n  color: #323338;\n  border-bottom: 1px solid #eee;\n  display: flex;\n  justify-content: space-between;\n  padding: 0 16px;\n  align-items: center;\n}\n\n.